import {
	getPhoneCode,
	validataCode,
	userRegister
} from '@/common/api/quickRegister.js';
export default {
	data() {
		return {
			codeNum: 0,
			phone: JSON.parse(uni.getStorageSync('userInfo')).phone,
			phoneNum: '', // 验证码
		}
	},
	methods: {
		// 获取手机验证码
		getPhoneCode: function() {
			this.codeNum = 60;
			let _this = this
			let time = setInterval(function() {
				if (_this.codeNum == 0) {
					clearInterval(time);
					return;
				}
				_this.codeNum--;
			}, 1000)

			getPhoneCode(this.phone).then(res => {}).catch(() => {
				uni.showToast({
					title: '发送失败，请检查网络',
					icon: 'none'
				})
			})
		},
		// 校验验证码
		validataCode: function() {
			if(this.phoneNum !== '' && this.phoneNum !== null && this.phoneNum !== undefined) {
				uni.showLoading({
					title: '加载中'
				})
				validataCode(this.phone, this.phoneNum).then(res => {
					console.log(res)
					if (res.data.code === 200) {
						this.$emit('getStep', 1)
					} else {
						uni.hideLoading()
						error('验证码错误')
				
					}
				}).catch(() => {
					error('网络')
				})
			}
			
		},
	}
}
